@import "../vars.less";
@import "../mixins.less";

 .gform_wrapper {
     
     margin: 100px 0;
     max-width: 100%;
     
     form {
     
        @media @small {
            fieldset {
               margin: 0 0 10px; 
            }

            label {
                display: none;

                &.box-label {
                    display: inline-block;
                    font-size: .8em;
                    color: @primary-grey;
                    padding-left: 5px;
                }
            }



            input[type="text"], input[type="email"], select {
                padding: 5px 10px;
            }

            .btn {
                padding: 5px 15px 7px; 
                font-size: .9em;
            }
        }

        @media @medium {

        }

        @media @large {

        }

        @media @extra-large {

        }
     
         
     }
 }

